<template>
  <div id="app">
    <linker></linker>
    <mainer></mainer>
    <qq :ispc="ispc"></qq>
    <img id="bg" src="./bg.jpg" />
  </div>
</template>

<script>
import linker from "./components/about_warma";
import qq from "./components/add_qq";
import mainer from "./components/main";
import Vue from "vue";
window.messagebox=new Vue();

export default {
  name: "App",
  components: {
    linker,
    qq,
    mainer
  },
  data(){
    return {
      ispc:null
    }
  },
  methods:{
    check(){
      var c=document.getElementById('content')
      var follow=document.getElementById('followbar')
      if(window.matchMedia("(max-width: 767px)").matches){
        this.ispc=false
        c.style.width=70+'%'
        follow.style.width=30+'%'
        window.messagebox.$emit('notpc')
      }else {
        this.ispc=true
        c.style.width=80+'%'
        follow.style.width=20+'%'
        window.messagebox.$emit('ispc')
      }
    }
  },
  mounted(){
    this.check()
    var __this=this
    window.onresize=__this.check()
    
  }
};
</script>

<style>
#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#linkbar {
  position: fixed;
  bottom: 0%;
  left: 30%;
  height: 2%;
  width: 50%;
}
#content {
  position: fixed;
  left: 0%;
  height: 95%;
  width: 70%;
}
#followbar {
  position: fixed;
  right: 0%;
  height: 30%;
  width: 30%;
}
#bg {
  position: fixed;
  left: 0%;
  top: 0%;
  width: 100%;
  height: 100%;
  z-index: -9999999;

  opacity: 0.5;
}
</style>
