<template>
    <el-card id="followbar">
        <el-row>
            <el-avatar src="./qqidlogo.jpg" id="qqidlogo" :size="50">
            </el-avatar>
            <span>Warma生物园</span>
            
        </el-row>
        <el-button :type="follow"  round @click="followit" id="qqfollow" :size="size" :icon="icon">{{add}}</el-button>
        
    </el-card>
</template>
<style>
    /*#followbar{
        width:50%;
        height: 50%;
        background-color: aqua;
    }
    #qqidlogo{
        position: absolute;
        right:45%;

    }
    #qqfollow{
        position: absolute;
        top:40px;
    }*/
</style>
<script>
import qqidlogo from "../assets/qqidlogo.jpg"
export default {
    props:["ispc"],
    data(){
        return {
            qqidlogo:qqidlogo,
            follow:"primary",
            icon:"el-icon-plus",
            add:"关注",
            size:"mini"
        }
    },
    methods:{
        open(m){
            window.open(m)
        },
        followit(){
            this.open("https://jq.qq.com/?_wv=1027&k=Ct95AONe")
            this.followed()
        },
        followed(){
            this.follow="success"
            this.icon="el-icon-check"
            this.add="已关注"
        },
        check(){
            if(this.ispc==true){
                this.size="middle"
            }
            else{
                this.size="mini"
            }
        },
        
    },
    mounted(){
        window.messagebox.$on('notpc',function(){
            this.size="mini"
            console.log('notpc ok')
        })
        window.messagebox.$on('ispc',function(){
            this.size="middle"
            console.log('ispc ok')
        })
    }
}
</script>